@import url(https://fonts.googleapis.com/css2?family=Spline+Sans:wght@400;600;700&display=swap);
:root {
  --color-title: rgba(45, 58, 148, 0.6);
  --color-cta-bg: rgba(45, 58, 148, 0.6);
  --color-cta-text: #ebbbb1;
  --color-highlight: #2d3a94;
  --color-text-main: rgba(45, 58, 148, 0.6);
}

html,
body,
