  width: 100%;
  overflow: hidden;
}

#root {
  overflow: auto;
}

body {
  position: fixed;
}

body {
  font-family: "Spline Sans", sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;

  color: rgba(45, 58, 148, 0.6);

  color: var(--color-text-main);
  background: #e8bab0;

  -webkit-user-select: none;

          user-select: none;
}

*::selection {
  background: transparent;
}

