  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  overflow-y: auto;
  will-change: transform;
}

section {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

section p {
  font-size: 32px;
  line-height: 38px;
}

.Overlay_sectionHero__e7zCX {
